import { Grid, Typography } from "@mui/material";
import { useEffect } from "react";
import useTimer from "../hooks/useTimer";
import React from "react";
import Loading from "../ui/Loading";
import { Link } from "react-router-dom";
const InProgress: React.FC = () => {
  const { renderTimer, timerFormat, timer, dueDate } = useTimer();
  useEffect(() => {
    renderTimer();
  }, [renderTimer]);

  const handleClickCheck = () => {
    localStorage.setItem("complete-date", JSON.stringify(timer));
  };
  const realDate = dueDate.format("YYYY. MM. DD. HH:MM");
  return timer === null ? (
    <Loading />
  ) : (
    <Grid
      container
      direction="column"
      justifyContent="center"
      alignItems="center"
      spacing={8}
    >
      <Grid item mb={-2}>
        <Typography variant={"sm"}> Onething </Typography>
      </Grid>
      <Grid item>
        <Typography variant={"lg"}> </Typography>
      </Grid>
      <Grid item>
        {timer !== null ? (
          <React.Fragment>
            <Typography variant={"xl"}>{timerFormat(timer.hour)} : </Typography>
            <Typography variant={"xl"}>
              {timerFormat(timer.minute)} :{" "}
            </Typography>
            <Typography variant={"xl"}>{timerFormat(timer.second)} </Typography>
          </React.Fragment>
        ) : null}
      </Grid>
      <Grid item mb={4}>
        <Typography variant={"xs"}>Due Date {realDate}</Typography>
      </Grid>
      <Grid item>
        <Link onClick={handleClickCheck} to="/complete">
          <img src={"/images/icons/check.svg"} alt={""} />
        </Link>
      </Grid>
    </Grid>
  );
};

export default InProgress;
