import dayjs from "dayjs";
import { useCallback, useState } from "react";

interface Timer {
  hour: number;
  minute: number;
  second: number;
}
const useTimer = () => {
  const [timer, setTimer] = useState<Timer | null>(null);
  const dueDate = dayjs(JSON.parse(localStorage.getItem("time") ?? ""));
  const renderTimer = useCallback(() => {
    setInterval(() => {
      const today = dayjs();
      const hour = dueDate.diff(today, "h");
      const minute = dueDate.diff(today, "m") % 60;
      const second = (dueDate.diff(today, "s") % 60) % 60;
      setTimer({
        ...timer,
        hour: hour,
        minute: minute,
        second: second,
      });
    }, 1000);
  }, [timer, dueDate]);
  const timerFormat = (num: number) => {
    return num < 10 ? `0${num}` : num.toString();
  };
  return { renderTimer, timerFormat, timer, dueDate };
};

export default useTimer;
